<template>
	<view class="container">
		<view class="tabHeader">
			<view class="id">序号</view>
			<view class="name">姓名</view>
			<view class="sex">性别</view>
			<view class="age">出生年月</view>
			<view class="operation">操作</view>
		</view>
		<view class="box" v-for="(item, index) in dataList" :key="item._id">
			<view class="id">{{index + 1}}</view>
			<view class="name">{{item.name}}</view>
			<view class="sex">{{sexFnMethods(item.sex)}}</view>
			<view class="age">{{item.birth}}</view>
			<view class="operation" v-on:click="del(item._id)">删除</view>
		</view>
	</view>
</template>

<script>
	import { sexFn, getQueryVariable } from "@/common/utils/tool.js";
	const todo = uniCloud.importObject('todo');
	export default {
		data() {
			return {
				dataList: [],
				type: 1
			}
		},
		onLoad: function (option) {
			this.type = option.id;
			this.getList(option.id);
		},
		methods: {
			async del(id){
				try {
					await todo.del(this.id, id).then(res=>{
						this.getList(this.id);
					})
					uni.showToast({
						title: '查询成功'
					})
				} catch (e) {
					uni.showModal({
						title: '查询失败',
						content: e.errMsg,
						showCancel: false
					})
				}
			},
			sexFnMethods(num){
				const txt = sexFn(num);
				return txt;
			},
			async getList(type){
				try {
					// const result = await todo.get();
					await todo.get(type).then(res=>{
						console.log("客户端====》", res);
						this.dataList = res.data || [];
					})
					uni.showToast({
						title: '查询成功'
					})
				} catch (e) {
					uni.showModal({
						title: '查询失败',
						content: e.errMsg,
						showCancel: false
					})
				}
			}
		}
	}
</script>

<style>
	.container {
		padding: 0 20px;
		font-size: 14px;
		line-height: 24px;
		    padding-top: 20px;
	}
	.box{
		display: flex;
		border-bottom: 1rpx dashed;
		    padding: 5px 0;
	}
	.id{
		width: 80rpx;
		padding-left: 10px;
	}
	.name{
		width: 150rpx;
	}
	.sex{
		width: 80rpx;
	}
	.age{
		width: 220rpx;
	}
	.tabHeader{
		    background: #5170c5;
		    padding: 4px 0;
		    width: 100%;
		    display: flex;
		    margin-top: 10px;
	}
</style>
